<template>
  <div>
    <input type="text" v-model="text">
    <button @click="websocketsend()">发送消息</button>  <br>
    <button @click="closeWebSocket()">关闭websocket连接</button><br>
    <button @click="initWebSocket()">建立websocket连接</button><br>
    <div>{{data}}</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      text: '',
      data: '',
      websocket: null
    }
  },
  // mounted() {
  //   if ('WebSocket' in window) {
  //     this.initWebSocket()
  //   } else {
  //     alert('当前浏览器 Not support websocket')
  //   }
  // },
  beforeDestroy() {
    if(this.websocket){
      this.closeWebSocket()
    }
  },
  methods: {
    initWebSocket() {
      this.websocket = new WebSocket('ws://192.168.2.240:61001/log')
      this.websocket.onerror = this.setErrorMessage//连接错误
      this.websocket.onopen = this.setOnopenMessage//连接成功
      this.websocket.onmessage = this.setOnmessageMessage//收到消息的回调
      this.websocket.onclose = this.setOncloseMessage//连接关闭的回调
    },
    setErrorMessage() {
      this.data = "WebSocket连接发生错误" + '   状态码：' + this.websocket.readyState;
    },
    setOnopenMessage() {
      this.data = "WebSocket连接成功" + '   状态码：' + this.websocket.readyState;
    },
    setOnmessageMessage(event) {
      this.data = '服务端返回：' + event.data;
    },
    setOncloseMessage() {
      this.data = "WebSocket连接关闭" + '   状态码：' + this.websocket.readyState;
    },
    
    websocketsend() {//websocket发送消息
      this.websocket.send(this.text)
      this.text = ''
    },
    closeWebSocket() {
      this.websocket.close()
    }
  }
}
</script>



